* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Source Sans Pro', sans-serif;
  font-size: 62.5%;
}
body {
  background-color: #EEEFF2;
  padding: 9rem 13.5rem;
}
p {
	font-weight: 400;
	font-size: 1.8rem;
	line-height: 2.8rem;
	margin-top: 0.5rem;
	letter-spacing: 0px;
}
img {
	width: 100%; 
}
h1 {
	font-size: 4.8rem;
	font-weight: 300;
	color: #333333;
	letter-spacing: -0.9px; 
	padding-bottom: 3rem;
}
h2 {
	font-size: 3rem;
	letter-spacing: -0.3px;
	font-weight: 700;
}

h3 {
	font-size: 2.4rem;
	letter-spacing: -0.3px;
	font-weight: 700;
}
.lead {
	font-weight: 400;
	font-size: 1.8rem;
}
h5 {
	font-size: 1.4rem;
	color: #888888;
	letter-spacing: 0.5px;
	text-align: left;
	font-weight: 600;
}
.small-dark {
	font-size: 1.6rem;
	color: #333333;
	letter-spacing: 0.5px;
	text-align: left;
	font-weight: 700;
}
.featurette-dark {
	font-size: 1.4rem;
	color: #333333;
	font-weight: 700;
}
/* Hero Grid  and Grid Items */
.heroGrid {
	align-items: center;
  background: white; 
	margin-bottom: 3rem;
}
.heroGridLeft {
	padding-right: 1.5rem;
} 
.heroGridRight {
	padding: 8rem 3rem 8rem 1.5rem;
} 
.hero-p {
	font-weight: 400;
	font-size: 1.8rem;
	line-height: 2.8rem;
	margin-top: 1rem;
	letter-spacing: 0px;
}
.featureLeft {
	padding-right: 2.1rem;
	box-sizing: border-box;  
} 
.featureRight {  
	padding: 0rem 1.5rem 0rem 0rem;
	box-sizing: border-box;
	padding-top: 2rem;
	padding-bottom: 2rem;
	padding-left: 1rem; 
} 
.featurette-p {
	font-size: 1.6rem; 
	line-height: 2rem;
}

/* World Traveler Chips */
.image-wrapper {
	position: relative;
}
.image-wrapper .chip-text {
	position: absolute;
	display: inline-block;
	padding: 4px 8px; 
	z-index: 2;
}
.image-wrapper .chip-yellow {
	background-color: #FFFFFF;
	color: #333333;
	font-weight: 600;
	top: 20px;
	left: -5px;
	border-left: 5px #F7B500 solid;
	font-size: 1.4rem;
	text-transform: uppercase;
}
.image-wrapper .chip-blue {
	background-color: #FFFFFF;
	color: #333333;
	font-weight: 600;
	top: 20px;
	left: -5px;
	border-left: 5px #32C5FF solid;
	font-size: 1.4rem;
	text-transform: uppercase;
}